@mixin mQ($arg...) {
    @if (length($arg)==1) {
        @media screen and (max-width: nth($arg, 1)) {
            @content;
        }
    }

    @if (length($arg)==2) {
        @media screen and (max-width: nth($arg, 1)) and (min-width: nth($arg, 2)) {
            @content;
        }
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Open Sans";
    background-color: #e8fbff
}

header {
    padding: 30px;
    width: 100%;
    height: 100px;
    background-color: rgba(0, 0, 0, 0.753);
    color: white;
}

header h1 {
    font-size: 50px;
    transform: translateY(-32%)
}

header nav ul li.main-menu-l {
    display: inline-block;
}

header nav ul li.main-menu-l #logo {
    display: grid;
    grid-template-columns: .1fr 1fr;
}

header #nav-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;

    #left-side ul li.main-menu-l {
        margin-left: 20px;

        @include mQ(991px) {}
    }

    #right-side {
        justify-self: right;
        transform: translateY(12%);

        #search-box {
            margin-right: 10px;
            padding: 5px;
        }

        li#search {
            position: relative;
        }

        li#search #search-icon {
            position: absolute;
            right: 9%;
            color: #646464;
            font-size: 20px;
            bottom: 17.5%
        }

        ul {
            list-style: none;
        }

        ul li {
            display: inline-block;
            margin-left: 20px;
        }

        li#icon {
            width: 50px;
            height: 50px;
            cursor: pointer;
            background: #ff0000;
            display: none;
            margin-bottom: -50px;
            position: relative;
        }

        .hamburger {
            width: 30px;
            height: 5px;
            background: #fff;
            position: absolute;
            top: 15%;
            right: 0;
            box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
            display: none;
        }

        .hamburger:before,
        .hamburger:after {
            content: '';
            position: absolute;
            width: 30px;
            height: 5px;
            background: #fff;
            box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
            transition: .5s;
        }

        .hamburger:before {
            top: -10px;
        }

        .hamburger:after {
            top: 10px;
        }
    }

    #browse-menu {
        position: absolute;
        background-color: rgba(0, 60, 82, 0.88);
        display: none;
        padding: 20px;
        z-index: 2;

        .browse-col-1 {
            float: left;
            width: 25%;
            margin: 0 0 0 0;
        }

        .browse-col-1 ul {
            list-style: none;
        }

        .browse-col-1 ul li {
            width: 150px;
            padding: 10px;
            margin: 0 0 0 0;
        }

        .browse-col-1 ul li:hover {
            color: #b9b9b9;
        }
    }
}

#alphabet {
    width: 100%;
    margin: auto;
    text-align: center;
    margin-top: 20px;

    li {
        display: inline-block;
        margin-left: 10px;
    }
}

#container {
    width: 1199px;
    margin: auto;
}



@include mQ(1400px) {
    #container {
        width: 80%;
    }

    .bottom-half-card{
        font-size: 14px;
    }


    header #nav-container {
        grid-template-columns: 1.5fr .5fr
    }

    header #nav-container #right-side li:nth-of-type(1),
    header #nav-container #right-side li:nth-of-type(2),
    header #nav-container #right-side li:nth-of-type(3) {
        display: none;
    }

    header #nav-container #right-side li#search #search-icon {
        right: 5%;
        font-size: 24px;
        bottom: 35%
    }

    #search-box {
        width: 350px;
        height: 40px;
        transform: translateY(-15%);
        font-size: 16px;
    }

    header #nav-container #right-side .hamburger {
        display: inline-block;
    }

    header #nav-container #left-side .main-menu-l:nth-of-type(2),
    header #nav-container #left-side .main-menu-l:nth-of-type(3),
    header #nav-container #left-side .main-menu-l:nth-of-type(4),
    header #nav-container #left-side .main-menu-l:nth-of-type(5) {
        font-size: 14px;
        display: none;
    }

    #search-box {
        width: 500px;
        height: 40px;
        transform: translateY(-15%);
        font-size: 16px;
    }

    header #nav-container #right-side li#search {
        transform: translateX(-20%);
    }
}

@include mQ(991px) {
    header #nav-container #left-side .main-menu-l {
        font-size: 14px;
    }

    header #nav-container #right-side li#search-box {}

    #search-box {
        width: 350px;
        height: 40px;
        transform: translateY(-15%);
        font-size: 16px;
    }

    .hamburger {
        display: block;
    }


}

@include mQ(768px) {
    #search-box {
        width: 200px;
        height: 40px;
        transform: translateY(-15%);
        font-size: 16px;
    }

    header #nav-container #right-side li#search #search-icon {
        transform: translateX(-20%);
    }
}

@include mQ(620px) {
    #search-box {
        display: none;
    }

    header #nav-container #right-side li#search #search-icon {
        color: white;
        transform: translate(-100%, 25%);
        transform: translate
    }
}


