// Hero

#hero {
    display: grid;
    grid-template-columns: .2fr .8fr;
    grid-gap: 25px;
    margin-top: 100px;
}

.hero-section:nth-of-type(1) {
    width: 100%;
    height: 100%;
    width: 240px;
    height: 360px;
}

.hero-section:nth-of-type(1) img {
    width: 100%;
    height: 100%;
}

.hero-section:nth-of-type(1) {
    //background-color:red;
    //background:url('/images/featured/coderealize.jpg');
    //background-position:center;
}

#hero-play-button {
    border: 1px solid black;
    width: 200px;
    padding: 15px 15px;
}

@include mQ(991px) {
    .hero-section:nth-of-type(1) {
        width: 160px;
        height: 240px;
    }

    .hero-section:nth-of-type(2) {
        font-size: 14px;
    }
}

@include mQ(768px) {
    #hero {
        display: block;
        text-align: center;
    }

    .hero-section:nth-of-type(1) {
        margin: auto;
        margin-bottom: 15px;
    }
}


#top-picks {
    margin-top: 50px;
}

#top-picks h2 {
    margin-bottom: 25px;
}


.top-card-group {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 50px;
    .diamond {
        position: absolute;
        content: "";
        width: 5px;
        background-color: white;
        height: 5px;
        margin-left: -12px;
        margin-top: 10px;
        transform: rotate(45deg);
    }

    .top-card {
        width: 100%;
        height: 300px;
        display: grid;
        grid-template-rows: .65fr .35fr;
    }

    .top-half-card {
        background: url('/images/featured/coderealize.jpg');
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 100%;
        background-color: black;
    }

    .bottom-half-card {
        display: grid;
        grid-template-columns: .4fr .6fr;
        position: relative;
        background-color: rgba(0, 0, 0, 0.76);
        color: white;
    }

    .bottom-half-card-helper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        position: absolute;
        top: 0;
    }

    .bottom-half-bg {
        background: url('/images/featured/coderealize.jpg');
        background-size: cover;
        background-position: center;
        width: 100px;
        height: 150px;
        transform: translate(25%, -50%)
    }

    .bottom-half-half:nth-of-type(2) {
        transform: translate(15%, 10px)
    } 


}

#show-more {
    display: block;
    text-align: center;
    width: 100%;
    margin: auto;
    margin-top: 50px;
    background-color: transparent;
    border: 1px solid black;
    padding: 10px;
}  

@include mQ(1400px) {
    .diamond {
        margin-top: 7.5px;
    }
}



@include mQ(1199px) {
    .top-card-group {
        grid-template-columns: 1fr 1fr;
    }
}
